//新闻列表组件
import NewsListItems from '@/components/news/news-list-items.vue'
import {
	newsList
} from '@/api/news.js'
export default {
	components: {
		NewsListItems
	},
	data() {
		return {
			dataList: []
		}
	},
	created() {
		newsList({}).then(res => {
			let {
				data
			} = res;
			this.dataList = data.data
		})
	}
}
